// require.ensure  Webpack  
/**
 * 1.
 * path : 
 * name  
 * component : 
 */

import Recommend from './components/recommend/recommend.vue';
import Welfare from './components/welfare/welfare.vue';
import App from './App.vue';
import Android from './components/lists/android.vue';
import IOS from './components/lists/ios.vue';
import Web from './components/lists/web.vue';
import wecome from './components/wecome/wecome.vue';

const routers = [
  {
    path: '/',
    name: 'index',
    component: App
  },

  {
    path: '/welfare',
    name: 'welfare',
    component: Welfare
  }, {
    path: '/day',
    name: 'day',
    component: Recommend,
    meta: {requiresAuth: true}
  },

  {
    path: '/ios',
    name: 'ios',
    component: IOS,
    meta: {requiresAuth: true}
  },

  {
    path: '/android',
    name: 'android',
    component: Android

  },
  {
    path: '/web',
    name: 'web',
    component: Web
  },

  {
    path: '/wecome',
    name: 'wecome',
    component: wecome

  }];

export default routers;



